<template>
    <div class="holder">
    
        <div class="item" v-for="(message,index) in navMessages" @touchend.stop.prevent="show(index)" @click="show(index)">
            <span class="icon">
               <img :src="message.icon_d" alt="" v-if="!message.show">
          
                  <img :src="message.icon" alt="" v-if="message.show">
            </span>
            <span class="title" v-bind:class="{ active: message.show }">{{message.item[activeLanguage]}}</span>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
    name: 'mynav',

    data() {
        return {
            navMessages: [
                {
                    "item": {

                        'chiness': '',
                        'english': 'route'

                    },
                    "icon": '/static/img/int_path@2x.png',
                     "icon_d": '/static/img/int_path_disabled@2x.png',
                    'show': false,
                    'layer': 'routeLayer',
                    'type': 'route',

                },
                {

                    "item": {

                        'chiness': '',
                        'english': 'sight'

                    },
                    "icon": '/static/img/int_jingdian@2x.png',
                    "icon_d": '/static/img/int_jingdian_disabled@2x.png',
                    'show': true,
                    'layer': 'sightLayer',
                    'type': 'sight',
                },
                {

                    "item": {

                        'chiness': '',
                        'english': 'wc'

                    },
                    "icon": '/static/img/int_WC@2x.png',
                    "icon_d": '/static/img/int_WC_disabled@2x.png',
                    'show': false,
                    'layer': 'wcLayer',
                    'type': 'wc',

                },
                {

                    "item": {

                        'chiness': '',
                        'english': 'food'

                    },
                    "icon": '/static/img/int_eat@2x.png',
                     "icon_d": '/static/img/int_eat_disabled@2x.png',
                    'show': false,
                    'layer': 'foodLayer',
                    'type': 'food',
                },
                {

                    "item": {

                        'chiness': '',
                        'english': 'park'

                    },
                    "icon": '/static/img/int_Park@2x.png',
                      "icon_d": '/static/img/int_Park_disabled@2x.png',
                    'show': false,
                    'class': 'jingqu',
                    'layer': 'parkLayer',
                    'type': 'park',
                },

                {

                    "item": {

                        'chiness': '',
                        'english': 'stop'

                    },
                    "icon": '/static/img/int_bus@2x.png',
                      "icon_d": '/static/img/int_bus_disabled@2x.png',
                    'show': false,
                    'layer': 'stopLayer',
                    'type': 'stop',
                },
                {

                    "item": {

                        'chiness': '',
                        'english': 'pano'

                    },
                    "icon": '/static/img/int_360@2x.png',
                    "icon_d": '/static/img/int_360_disabled@2x.png',
                    "url": "",
                    'show': false,
                    'layer': 'panoramicLayer',
                    'type': 'panoramic',
                },
                {

                    "item": {

                        'chiness': '',
                        'english': 'other'

                    },
                    "icon": '/static/img/int_other@2x.png',
                    "icon_d": '/static/img/int_other_disabled@2x.png',
                    "url": "",
                    'show': false,
                    'layer': 'otherLayer',
                    'type': 'other',
                },
            ]

        }
    },
    mounted() {


    },
    created: function () {

    },
    computed: mapState([ 'activeLanguage']),
    methods: {
        show(index) {
            this.navMessages[index].show = !this.navMessages[index].show;
            this.$emit('change', {
                'state': this.navMessages[index].show,
                'type': this.navMessages[index].type,
            })
        }
    },


}
</script>


<style scoped lang="less">
.holder {
    // display: flex;
    // justify-content: center;
    // align-items: center;
    //flex-wrap: wrap;
    padding: 0.86rem 1.07rem;
    width: 19.43rem;
    height: 10.86em;
    background: #fff;
    border-radius: 0.5rem;
    border: none;
    &::before {
        content: '';
        width: 0;
        height: 0;
        border: 0.6rem solid transparent;
        border-top-color: #FFF;
        position: absolute;
        right: 15%;
        bottom: -1.1rem;
    }
    .item {
        background: #FFF;
        min-width: 25%;
        padding-bottom: 8px;
        padding-top: 8px;
        float: left;
        &:hover {
            cursor: pointer;
            background: #FFF;
        }
        span.icon {
            width: 1.71rem;
            height: 1.71em;
            display: block;
            margin: 0 auto;
            position: relative;
            img {
                width: 100%;
                height: 100%;
            }
            .cover {
                position: absolute;
                top: 0px;
                left: 0px;
                width: 1.71rem;
                height: 1.71em;
                display: block;

             
            }
        }
        span.title {
            text-align: center;
            display: block;
            margin-top: 0.5rem;
            color:#999;
            &.active{
                 color:#000;
            }
        }
    }
}
</style>





